<template>
  <div>
    <div class="light-grey d-flex justify-content-center">
      <div>
        <img :src="image" />
        <h1>Hey, cette page n'existe pas !</h1>
          <div class="d-grid gap-2">
          <button type="button" class="btn btn-danger button" @click="toHome()">Retour à l'accueil</button>
          </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 404,
  computed: {
    image() {
      let random = Math.floor(Math.random() * Math.floor(100))
      if (random == 0) random += 1
      if (random < 10) {
        random = '00' + random
      }
      if (random > 9) {
        random = '0' + random
      }
      return process.env.VUE_APP_POKEMON + random + '.png'
    },
  },
  methods: {
    toHome(){
      window.location = "/"
    }
  }
}
</script>

